@mixin desktopUp {
    @media (min-width: 1300px) {
        @content;
    }
}

@mixin laptopUp {
    @media (min-width: 1100px) {
        @content;
    }
}

.burnout-index-table-data-container {
    margin-right: 0;
    padding: 20px;
    @include desktopUp {
        width: auto;
        margin-right: 0;
        margin-bottom: 25px;
        flex: 0 0 auto;
    }
    table {
        min-width: 100%;
    }
    .paperStyles{
        height: 100%;
        margin-bottom: 0;
    }
    tbody{
        tr{
            &:last-child{
                th,td{
                    font-weight: 700;
                }
            }
        }
    }
    .heading-container{
        width: calc(100% - 40px);
        margin-left: auto;
        margin-right: auto;
        margin-bottom: -36px;
        position: relative;
        box-shadow: 0 2px 4px -2px rgba(0,0,0,.24), 0 4px 24px -2px rgba(0,0,0,.2);
        background-color: #4cb1a1;
        border-radius: 16px;
        padding: 16px;
        h3{
            font-size: 25px;
            color: #fff;
            font-weight: 700;
            text-align: center;
            margin-top: 0;
            margin-bottom: 2px;
        }
        .desc{
            font-size: 17px;
            color: #fff;
            text-align: center;
            margin: 0;
        }
    }
    .table-container{
        box-shadow: 0px 0px 20px 0px rgba(0,0,0,.12);
        padding-top: 40px;
        border-radius: 8px;
    }
    .table-data-container{
        max-width: 500px;
        margin: 0 auto;
    }
    table{
        border-radius: 4px;
        overflow: hidden;
    }
    td,th{
        font-size: 16px;
    }
    td{
        text-align: center;
    }
}