.TodoSideNavContainer{
    display: flex;
    flex-direction: column;
    background-color: #fff;
    width: 450px;
    border: 1px solid #ccc;
    transition: all 300ms ease;
    position: fixed;
    z-index: 1;
    height: 100%;
    top: 0;
    right: -100%;
    &.open{
        right: 0;
    }
    h2{
        margin: 0;
    }
    .todo-title{
        color: #3c3c3c;
        font-size: 1.25rem;
        font-weight: 700;
        line-height: 1.5;
        margin: 0px 0px 26px;
    }
    .todo-status{
        font-size: 0.85rem;
        text-transform: uppercase;
        font-weight: 700;
        padding: 8px 8px;
        border-radius: 4px;
        margin-bottom: 0;
        display: inline-block;
        margin-right: auto;
        &.ongoing{
            color: rgb(0, 171, 85);
            background-color: rgba(0, 171, 85, 0.08);
        }
    }
    .todo-detail{
        color: rgb(145, 158, 171);
        font-size: 0.9rem;
        margin: 2px 0;
        display: flex;
        align-items: center;
    }
}
.side-nav-wrapper{
    padding: 20px;
    height: calc(100vh - 198px);
    display: flex;
    flex-direction: column;
}
.moment-form-wrapper{
    border-top: 1px solid #ccc;
    margin-top: auto;
    padding: 20px;
}
.moment-type-label,
.moment-form-title{
    color: #646464;
    margin: 0;
}

.todo-detail{
    color: rgb(145, 158, 171);
    font-size: 0.9rem;
    margin: 10px 0;
    display: flex;
    align-items: flex-start;
    transition: color 300ms ease;
    &.clickable{
        &:hover{
            cursor: pointer;
            color: rgb(0, 171, 85);
        }
    }
    .label{
        margin-right: 10px;
    }
    .icon{
        margin-right: 5px;
    }
}