.PostSliderContainer, .PostSliderContainer * {
    min-height: 0;
    min-width: 0;
}
.slick-prev, .slick-next {
    font-size: 0;
    line-height: 0;
    position: absolute;
    top: 50%;
    display: block;
    width: 20px;
    height: 20px;
    padding: 0;
    -ms-transform: translate(0, -50%);
    transform: translate(0, -50%);
    cursor: pointer;
    color: transparent;
    border: none;
    outline: none;
    background: transparent;
}

.PostSliderContainer{
    display: flex;
    width: 100%;
    // max-width: 614px;
    .slick-dots{
        display: flex!important;
        align-items: center;
        justify-content: center;
        bottom: -11px;
        li{
            margin: 0;
        }
        button{
            position: relative;
            padding: 0;
            display: flex;
            align-items: center;
            justify-content: space-between;
            padding: 0;
            &:before{
                content: "";
                background-color: #DBDBDB;
                width: 8px;
                height: 8px;
                border-radius: 8px;
                opacity: 1!important;
                position: static;
                margin: 0 auto;
                transition: all 100ms ease;
            }
        }
        .slick-active{
            button{
                &:before{
                    width: 12px;
                    height: 12px;
                    background-color: #008577;
                }
            }
        }
    }
    .slick-arrow {
        display: block;
        top: auto;
        z-index: 2;
        width: auto;
        // font-size: 14px;
        bottom: 7px;
        &:after{
            font-size: 14px;
            color: #008577;
        }
        &:before{
            content: "";
            position: absolute;
            border-top: 2px solid #008577;
            border-right: 2px solid #008577;
            top: 50%;
            width: 10px;
            height: 10px;
        }
        &.slick-next,
        &.slick-prev{
            display: flex!important;
            align-items: center;
        }
        &.slick-next{
            right: 0;
            padding-right: 20px;
            &:after{
                content: "Next";
            }
            &:before{
                right: 2px;
                transform: translateY(-50%) rotate(45deg);
            }
        }
        &.slick-prev{
            left: 0;
            padding-left: 20px;
            &:after{
                content: "Prev";
            }
            &:before{
                left: 2px;
                transform: translateY(-50%) rotate(-135deg);
            }
        }
    }
}
.slick-dots {
    list-style: none;
    padding: 0;
    li button {
        font-size: 0;
        line-height: 0;
        display: block;
        width: 20px;
        height: 20px;
        padding: 5px;
        cursor: pointer;
        color: transparent;
        border: 0;
        outline: none;
        background: transparent;
    }
}

