.date-wrapper{
    p{
        margin: 0;
    }
}
.moment-list-wrapper{
    background-color: #fafafa;
    height: calc(100vh - 485px);
    overflow: auto;
}
.momentItem{
    position: relative;
    background-color: #fff;
    padding: 10px;
    p{
        margin-top: 0;
        font-size: 0.9rem;
        color: #212121;
    }

    .moment-more-option-trigger{
        position: absolute;
        top: 4px;
        right: 4px;
    }
}
.moment-type{
    background-color: rgba(0, 171, 85, 0.08);
    color: rgb(0, 171, 85);
    border: 1px solid rgb(0, 171, 85);
    border-radius: 4px;
    padding: 4px 8px;
    font-size: 0.75em;
    font-weight: 700;
    letter-spacing: 0.5px;
    display: inline-block;
    margin-bottom: 10px;
}