.moment-choices{
    list-style: none;
    display: flex;
    align-items: center;
    padding-left: 0;
    svg{
        color: #b6b6b6;
        display: block;
        transition: color 300ms ease;
    }
    li{
        margin-left: 10px;
        &.active{
            svg{
                color: rgb(0, 171, 85);
            }
        }
    }

}
.submit-moment{
    svg{
        color: rgb(0, 171, 85);
    }
}