.line-and-bar-graph{
    .dataLegends{
        list-style: none;
    }
    .legend-container{
        display: flex;
        align-items: center;
        margin: 0;
    }
    .graphContainer{
        display: flex;
        align-items: flex-start;
        &.has-side-text{
            padding-left: 15px;
        }
    }
    .legend{
        width: 24px;
        height: 14px;
        display: block;
        margin-right: 10px;
        font-size: 0;
    }
    .rv-xy-plot__series--line{
        fill: transparent;
    }
    .label{
        display: block;
    }
}
.dataLegends {
    padding: 20px 0 10px;
    margin: 0;
    width: 100%;
    max-width: 490px;
    height: 66px;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    list-style: none;
}