.NotesContainer{
    border-radius: 10px;
    box-shadow: rgb(145 158 171 / 20%) 0px 0px 2px 0px, rgb(145 158 171 / 12%) 0px 12px 24px -4px;
}
.NotesContainer{
    position: relative;
    width: 100%;
}
.note-content{
    height: 300px;
    // border: 1px solid #ccc;
    // border-radius: 4px;
    padding: 20px;
    overflow: auto;
}
.NoteEditorContainer{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #fff;
    z-index: 1;
    
    .ck-editor__main{
        .ck-content{
            height: calc(100% - 96px);
        }
    }
    .ck.ck-editor{
        margin-bottom: 20px;
        height: calc(100% - 64px);
        .ck-content{
            width: 100%;
            height: 100%;
        }
        .ck-editor__main{
            height: calc(100% - 39px);
        }
    }
}
.note-content{
    .content-action-wrapper{
        opacity: 0;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        right: 0;
    }
    p,li{
        position: relative;
        transition: all 300ms ease;
        background-color: #fff;
        &:hover{
            background-color: rgb(227, 255, 221);
            .content-action-wrapper{
                opacity: 1;
            }
        }
    }
}
.note-header{
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: rgb(244, 246, 248);
    border-bottom: 1px solid rgba(145, 158, 171, 0.24);
    padding: 10px 15px;
    h2{
        font-size: 1rem;
        color: rgb(33, 43, 54);
        margin: 0;
    }
}
.note-list-wrapper{
    display: flex;
    flex-wrap: wrap;
    .NotesContainer{
        width: calc(50% - 20px);
        margin: 10px;
    }
}