
.TodoContainer{
    position: relative;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    padding: 20px 20px 12px 20px;
    border-bottom: 1px solid #ccc;
    background-color: #fff;
    transition: all 300ms ease;
    &:hover{
        background-color: rgba(0, 133, 119, 0.1);
    }
    .name{
        color: #008577;
        font-weight: 500;
        margin-top: 0;
    }
    .description{
        color: #777;
        font-weight: normal;
        margin: 16px 0 0;
    }
    .TodoMoreOptionsContainer{
        position: absolute;
        z-index: 2;
        right: 20px;
        top: 10px;
    }
    .actions-container{
        position: relative;
        z-index: 2;
        margin-top: 26px;
        button{
            margin-left: 0;
        }
    }
}
.todo-info {
    .name{
        font-size: 14px;
        cursor: pointer;
    }
}