@mixin wideUp {
    @media (min-width: 1730px) {
        @content;
    }
}
@mixin desktopUp {
    @media (min-width: 1300px) {
        @content;
    }
}
@mixin laptopUp {
    @media (min-width: 1100px) {
        @content;
    }
}
@mixin mobileUp {
    @media (min-width: 590px) {
        @content;
    }
}

.DashboardContainer{
    background-color: #fff;
    display: block;
    position: fixed;
    top: 0;
    bottom: 0;
    right: -100%;
    width: 80%;
    z-index: 3;
    padding: 20px;
    box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
    transition: right 300ms ease;
    overflow: auto;
    &.open{
        right: 0;
    }
    h2{
        margin: 0;
    }
    .heading-container{
        h5{
            margin: 0 0 20px;
            font-size: 16px;
            color: #008577;
        }
    }
}
.top-panel{
    @include wideUp{
        display: flex;
        flex-wrap: wrap;
    }
    .WeeklyGraphSliderContainer{
        margin-bottom: 4px;
        @include wideUp{
            width: 800px;
        }
    }
}
.bottom-panel{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    @include desktopUp{
        justify-content: flex-start;
    }
    .burnout-index-table-data-container{
        width: 100%;
        margin-bottom: 20px;
        @include laptopUp{
            width: calc(45% - 10px);
            margin-bottom: 0;
        }
        @include desktopUp{
            width: 450px; 
            margin: 0 20px 0 0;
        }
    }
    .productivity-data-container{
        padding: 20px;
        width: 100%;
        @include laptopUp{
            width: calc(55% - 10px);
        }
        @include desktopUp{
            width: 450px; 
        }
        // margin-right: 20px;
    }
}

.doughnutChartWithTableSectionContainer {
    .rv-xy-plot__series--label-text, 
    .workspaceBugList .sprintWorkspaceActiveBugContainer .rv-xy-plot__series--label-text {
        stroke: #fff;
        color: #fff;
        fill: #fff;
    }
    .custom-data-legends{
        list-style: none;
        padding: 20px 0 0;
        width: 310px;
        @include mobileUp{
            padding-top: 0;
        }
        .legend-data{
            display: flex;
            align-items: center;
            margin-bottom: 10px;
            margin-left: 0;
        }
        .labelColor{
            display: block;
            height: 20px;
            flex: 0 0 20px;
            border-radius: 100%;
            font-size: 0;
            margin-right: 10px;
        }
    }
    .dataGraphContainer{
        & > div{
            margin: 0 auto;
        }
    }
    .legend-data{
        font-size: 1rem;
    }
    .tableDataContainer{
        width: 100%;
        max-width: 450px;
    }
    .graph-and-table-data-container{
        @include mobileUp{
            display: flex;
        }
    }
    .dataGraphContainer{
        padding-right: 24px;
        display: flex;
    }
}