@mixin wideUp {
    @media (min-width: 1730px) {
        @content;
    }
}
@mixin desktopUp {
    @media (min-width: 1300px) {
        @content;
    }
}

@mixin laptopUp {
    @media (min-width: 1100px) {
        @content;
    }
}
@mixin tabletUp {
    @media (min-width: 768px) {
        @content;
    }
}
@mixin mobileUp {
    @media (min-width: 590px) {
        @content;
    }
}

.weekly-average-data-container{
    margin-bottom: 20px;
    width: 100%;
    @include wideUp{
        flex: 0 0 calc(100% - 830px);
        margin-right: auto;
    }
    // width: 100%;
    // max-width: 600px;
    .card-title{
        display: flex;
        align-items: center;
        margin-bottom: 4px;
    }
    .icon{
        font-size: 10px;
        margin-right: 4px;
        svg{
            width: 14px;
            height: 14px;
            display: block;
        }
    }
    .text{
        font-size: 14px;
        font-weight: 700;
        color: #75869d;
    }
    .value{
        text-align: center;
        font-size: 40px;
        font-weight: 700;
        color: #75869d;
        margin: 10px auto 0;
    }
    .percent-comparison-container{
        font-size: 14px;
        font-weight: 700;
        color: #75869d;
        min-height: 19px;
    }
    .graphContainer{
        background-color: #fff;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        border-radius: 1px;
        box-shadow: 0px 2px 1px -1px rgba(0,0,0,.20), 0px 1px 1px 0px rgba(0,0,0,.14), 0px 1px 3px 0px rgba(0,0,0,.12);
        flex: 0 0 100%;
        height: 150px;
        margin-bottom: 10px;
        @include mobileUp{
            flex: 0 0 calc(50% - 5px);
            height: 177px;
        }
        @include desktopUp{
            flex: 0 0 calc(33.3333% - 13px);
            height: 226px;
        }
        @include wideUp{
            flex: 0 0 calc(50% - 5px);
            // height: 177px;
        }
    }
    p{
        text-align: center;
    }
    .percent-comparison{
        &.red{
            color: red;
        }
        &.green{
            color: green;
        }
    }
    .card-list-container{
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        .graphContainer:last-child{
            @include wideUp{
                margin-right: 0;
            }
        }
        // .graphContainer{
        //     height: 226px;
        // }
    }
}